import Data from "@/js/core/data";

const KB_Templates_Server = "https://api.${region}.500apps.com/support/kb/";

class KBTemplates extends Data {
  constructor() {
    super(KB_Templates_Server);
    this.disablePCors();
  }
  async get_KB_details(domain) {
    let url = `${KB_Templates_Server}${domain}`;
    this.url = eval("`" + url + "`");
    return await super.get();
  }
}
export default new KBTemplates();
