import Vue from "vue";
import Vuex from "vuex";
import { Device } from "twilio-client";
import axios from "axios";
import { bus } from "./emit-bus";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    device: null,
    token: null,
    status: false,
    tokenUrl: null,
    deviceStatus: "",
  },
  mutations: {
    setTokenUrl(state, payload) {
      state.tokenUrl = payload;
    },
    setToken(state, payload) {
      state.token = payload;
    },
    setTwilioStatus(state, payload) {
      state.status = payload;
    },
    setDevice(state, payload) {
      state.device = payload;
    },
  },
  actions: {
    // eslint-disable-next-line no-unused-vars
    async disconnectDevice(context, data) {
      context.getters.device.disconnectAll();
    },
    // eslint-disable-next-line no-unused-vars
    async connectToTwilio(context, payload) {
      if (context.getters.token) {
        Device.setup(context.getters.token, {
          // Set Opus as our preferred codec. Opus generally performs better, requiring less bandwidth and
          // providing better audio quality in restrained network conditions. Opus will be default in 2.0.
          codecPreferences: ["opus", "pcmu"],
          // Use fake DTMF tones client-side. Real tones are still sent to the other end of the call,
          // but the client-side DTMF tones are fake. This prevents the local mic capturing the DTMF tone
          // a second time and sending the tone twice. This will be default in 2.0.
          fakeLocalDTMF: true,
          enableRingingState: true,
        });

        Device.ready(function() {
          context.commit("setTwilioStatus", true);
          context.commit("setDevice", Device);
          context.dispatch("twilioDeviceListeners");
        });

        // eslint-disable-next-line no-unused-vars
        Device.on("error", function(error) {});
      }
    },
    // eslint-disable-next-line no-unused-vars
    twilioDeviceListeners(context, payload) {
      context.state.device.on("incoming", function(conn) {
        let conn_obj = {};
        conn_obj.conn = conn;
        conn_obj.type = "incoming";
        // Emit the incoming call
        bus.$emit("incoming", conn_obj);
      });

      context.state.device.on("disconnect", function(conn) {
        if (context.state.deviceStatus != "disconnect") {
          context.state.deviceStatus = "disconnect";
          var disconnect_data = {};
          disconnect_data.direction = conn.direction;
          disconnect_data.callSid = conn.parameters.CallSid;
          disconnect_data.toNumber = conn.message.To;
          disconnect_data.type = "Disconnect Call";
          // parent.window.postMessage("sjdghsds", "*");
          window.parent.postMessage(disconnect_data, "*");
          bus.$emit("user_call_hangup");
        }
      });

      // eslint-disable-next-line no-unused-vars
      context.state.device.on("connect", function(conn) {
        context.state.deviceStatus = "connect";
      });

      // Added this event to close the popup when an incoming connection is canceled by the caller before it is accepted by the Twilio Client device
      context.state.device.on("cancel", (conn) => {
        context.state.deviceStatus = "cancel";
        var disconnect_data = {};
        disconnect_data.direction = conn.direction;
        disconnect_data.callSid = conn.parameters.CallSid;
        disconnect_data.type = "Disconnect Call";
        window.parent.postMessage(disconnect_data, "*");
      });
    },

    // Get details from capability url
    async getToken(context, url) {
      let response = await axios.get(url);
      context.commit("setToken", response.data.token);
      context.commit("setTokenUrl", url);
      context.dispatch("connectToTwilio");
      return response.data.context;
    },
  },
  getters: {
    tokenURl: (state) => {
      return state.tokenUrl;
    },

    device: (state) => {
      return state.device;
    },
    token: (state) => {
      return state.token;
    },
    isTwilioReady: (state) => {
      return state.status;
    },
    checkDeviceStatus: (state) => {
      return state.deviceStatus;
    },
  },
  modules: {},
});
