<template>
  <div id="app">
    <b-container fluid class="px-0 overflow-hidden">
      <b-row class="mx-0">
        <b-col sm="12" class="px-0">
          <b-navbar variant="primary" v-if="showStickyBar() && strip" sticky>
            <p class="mx-auto mb-0 text-light">You are previewing the page from 500apps. You can use your own domain to remove this message.</p>
          </b-navbar>
          <router-view v-bind="{ template: template_data }" />
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
  export default {
    name: "App",
    data() {
      return {
        host: window.location.host,
        strip: true,
        title: "",
        favicon: "",
        template_data: "<div></div>",
      };
    },
    watch: {
      $route: {
        handler: async function(to) {
          // Whenever valid route exists then only set the favicon & page title dynamically based on App.
          if (to.name) {
            // For support cname based on props we are setting the title & favicon.
            if (window.app == "support" && to.name !== "SupportFeedback") await this.getKBTemplateDetails();

            document.title = this.title || to.meta.title || "500apps - Grow like the Fortune 500";

            // Add dynamic favicon
            const link = document.querySelector("[rel='icon']");
            if (to.meta.icon) link.setAttribute("href", `https://infinity.500apps.com/img/logos/${to.meta.icon}.svg`);
            else link.setAttribute("href", "https://infinity.500apps.com/img/infinity.svg");
          }
        },
        immediate: true,
      },
    },
    mounted() {
      // this.host = "sign-dev-1744-3172.public.500apps.org";
      // Hide strip when location queryparams has type is widget
      const params = new URLSearchParams(window.location.search);
      let condition =
        window.location.href.includes("conference") ||
        window.location.href.includes("500conference") ||
        window.location.href.includes("ninjacapture") ||
        window.location.href.includes("schedule") ||
        window.location.href.includes("ninjainterview");
      if (params.get("type") == "widget" || condition) this.strip = false;
    },

    methods: {
      // Restrict sticky bar for cname & 404 pages
      showStickyBar() {
        try {
          // Regular expression accepts all regions dev, qa,production for public URL
          let regex = /public(.*?).500apps/gm;
          return regex.test(this.host.toLowerCase()) && this.$router.history.current.name && this.$router.history.current.name != "404 page";
        } catch (error) {
          console.log(error);
        }
      },
      async getKBTemplateDetails() {
        if (this.template_data !== "<div></div>") return;
        const KBTemplate = require("@/js/support/cname/kb-templates");
        try {
          let data = await KBTemplate.default.get_KB_details(window.domain);
          if (data.length) {
            if (data[0]?.kb_template) this.template_data = `<div>${data[0].kb_template.replace("Categories", "router-view")}</div>`;
            let regex = /{(.*?)}/gm;
            let props = eval("(" + this.template_data.match(regex)[0] + ")");
            // If the KB template have the title & favicon props then only we are setting the dynamic values otherwise default
            this.title = props?.title ? props.title : "";
            this.favicon = props?.favicon_url ? props.favicon_url : "";
          } else this.template_data = `<div><Header/><router-view/><Footer/></div>`;
        } catch (error) {
          this.template_data = `<div><Header/><router-view/><Footer/></div>`;
          console.log(error);
        }
      },
    },
  };
</script>
<style lang="scss"></style>
