import Vue from "vue";

import router from "@/router/index";
import App from "@/App.vue";
import { BootstrapVue } from "bootstrap-vue";
import axios from "axios";
import store from "../dialer/twilio";
import { decompressFromEncodedURIComponent as lzStringDecompress } from "lz-string";

Vue.use(BootstrapVue);

const Init = async () => {
  // URL will come as {app}-{region}-{env}-{domain}-{user-id_optional}
  // let host = window.location.host;
  // URL for cname testing purpose added
  // let host = "schedule500apps.appup.ch";
  // URL for public page tetsing purpose added
  let host = "https://forms-dev-941-1080-1432-2120.public-dev.500apps.org/";

  // Regular expression accepts all regions dev, qa,production for public URL
  let regex = /public(.*?).500apps/gm;

  // Decompress ninjacapture token ,which is from location path
  if (host.includes("drive.500apps.com") || host.includes("ninjacapture")) {
    // Check compatibility (Here 8 is nothing but shortcut url length)
    var shortcutURL = location.pathname.split("/")[1].length === 8;
    var region = location.search.split("=")[1];
    // Get path from the shortcut url or LZString url
    if (shortcutURL) {
      var baseURL = await axios.get(`https://api${region ? "." + region : ""}.500apps.com/shortcuts/shortcut${location.pathname}`);
      var url = baseURL.data[0].link;
    } else url = location.pathname;
    // Decompress ninjacapture token ,which is from location path
    host = window.location.href.includes("/nc") ? lzStringDecompress(url.split("/")[shortcutURL ? 4 : 2]) : lzStringDecompress(url.split("/")[shortcutURL ? 3 : 1]);
  } else if (host.includes(".schedule.cc")) window.history.pushState("", "", `/schedule`);
  else if (host.includes(".recruithire.com")) window.history.pushState("", "", `/recruithire`);
  else if (host.includes(".ninjainterview.com")) window.history.pushState("", "", `/ninjainterview`);
  else if (!host.toLowerCase().match(regex) && !host.includes(".conference.ly") && !host.includes(".500conference.com")) {
    var response = await axios.get(`https://dns.google/resolve?name=${host}&type=cname`);
    // Navigate 404 route based on dns server response.It shoud be vary from app to app
    if (!response.data || !response.data.Answer || response.data.Answer.length == 0) {
      // Redirecting to 404 page
      window.history.pushState("", "", `/404`);
      return;
    }
    // Set the actual host for c-name pages
    host = response.data.Answer[0].data;
  }

  window.app = host.split("-")[0].toLowerCase();

  window.region = host.split("-")[1];
  // window.env = host.split("-")[2];
  window.domain = host.split(".")[0].split("-")[2];

  // Optional
  try {
    window.user = host.split(".")[0].split("-")[3];
  } catch (error) {
    console.log(error);
  }
  // Optional id
  try {
    window.id = host.split(".")[0].split("-")[4];
  } catch (error) {
    console.log(error);
  }
  if (host.includes("feedbackup")) {
    try {
      // Check region
      if (((host.split("-")[1] == "dev" || host.split("-")[1] == "qa") && host.split("-").length == 6) || host.split("-").length == 5) {
        window.user = host.split("-")[3];
        window.project_id = host.split("-")[4].split(".")[0];
      } else {
        window.user = host.split("-")[4];
        window.project_id = host.split("-")[3];
        window.fid = host.split("-")[5].split(".")[0];
      }
    } catch (error) {
      console.log(error);
    }
  }
  if (host.includes("forms")) {
    try {
      window.project_id = host.split(".")[0].split("-")[3];
      window.id = host.split(".")[0].split("-")[5];
    } catch (error) {
      console.log(error);
    }
  }

  if (host.includes("schedule")) {
    try {
      window.project_id = host.split(".")[0].split("-")[3];
      window.user = host.split(".")[0].split("-")[4];
      window.id = host.split(".")[0].split("-")[5];
    } catch (error) {
      console.log(error);
    }
  }
  if (host.includes("ninjainterview")) {
    try {
      window.position_id = host.split(".")[0].split("-")[3];
      window.test_id = host.split(".")[0].split("-")[4];
    } catch (error) {
      console.log(error);
    }
  }
  // Redirecting to app page if there is no pathname in url
  if (window.app && window.location.pathname == "/" && !host.includes(".conference.ly") && !host.includes(".500conference.com")) {
    window.history.pushState("", "", `/${window.app}`);
  }
  loadRouter();
};
// If c-name exists then loading routes after getting c-name response
// It works for public pages also
const loadRouter = () => {
  new Vue({
    router,
    store,
    render: (h) => h(App),
  }).$mount("#app");
};

// Invoke init method
Init();
